<template >
    <div>
        <search></search>
        <div class="content-warper" v-if="!searchShow">
	      <swiper :options="swiperOption" class="swiper-box">
	        <swiper-slide class="swiper-item">
	        	<recommand></recommand>
	        </swiper-slide>
	        <swiper-slide class="swiper-item">
	        	<rank></rank>
	        </swiper-slide>
	        <div class="swiper-pagination" slot="pagination"></div>
	      </swiper>
	    </div>
    </div>
</template>

<script>
	import Search from './Search'
	import Recommand from './Recommand'
	import Rank from './Rank'
	
	import {mapGetters} from 'vuex'
	
	import {
		swiper,
		swiperSlide
	} from 'vue-awesome-swiper'
	
	const TAB_NAME = ['推荐', '排行榜']
	
export default {
    components:{
    	swiper,
		swiperSlide,
    	Search,
    	Recommand,
    	Rank
    },
    data(){
    	return {
			swiperOption: {
				pagination: '.swiper-pagination',
				paginationClickable: true,
				paginationBulletRender(swiper, index) {
					return `<span class="swiper-pagination-bullet swiper-pagination-bullet-custom">${TAB_NAME[index]}</span>`
				}
			}
    	}
    },
    computed:{
		...mapGetters(
			['searchShow']
		),
	},
}
</script>

<style>
</style>


