export default {
	searchShow(state){
    	return state.searchShow;
  	},
  getPauseBtn(state){
  	return state.pauseBtn;
  },
  getCurrentTime(state){
  	let
  		m = parseInt(state.currentTime/60),
  		s = parseInt(state.currentTime - m * 60)
  		s < 10?s = '0' + s:'';
  	return m + ":" + s;
  },
  getDuration(state){
  	let
  		m = parseInt(state.duration/60),
  		s = parseInt(state.duration - m * 60)
  		s < 10?s = '0' + s:'';
  	return m + ":" + s;
  },
  getProgress(state){
	return (state.currentTime / state.duration * 100).toFixed(1) + '%'
  },
  getMusic(state){
  	return state.music
  },
  //获得播放列表
  getPlayingList(state){
  	return state.playinglist
  },
  //获得播放模式
  getPlayMode(state){
  	switch(state.PlayMode){
  		case 0:
  			return [state.PlayMode,'顺序播放']
  		case 1:
  			return [state.PlayMode,'随机播放']
  		case 2:
  			return [state.PlayMode,'单曲循环']
  	}
  }
}



