<template>
	<div id="lyric">
		<p class="lyric-item" v-for="(v,k) in mylyric" v-if="k<3">{{v}}</p>
		<p class="lyric-item">{{currentLyric(getCurrentTime)}}</p>

	</div>
</template>

<script>
	//导入解密文件
	import Base64 from '../utils/base64'

	import { mapGetters } from 'vuex'
	export default {
		data() {
			return {
				lyric: null,
				musicId: this.$store.state.music.musicId,
				currentTime: this.$store.state.currentTime,
				mylyric: [],
				time: 0,
			}
		},
		updated() {//7096775
			this.$store.dispatch('getLyric', this.musicId).then((res) => {
				this.lyric = Base64.decode(res.data.lyric)
					.split('[')
					.slice(5)
					.map(str => {
						let t = str.split(']')
						t[0] = parseInt(t[0].split(':')[0]) * 60 + parseInt(t[0].split(':')[1])
						return {
							[t[0]]: t[1]
						}
					})
					.reduce((a, b) => {
						return { ...a, ...b }
					})
			})
		},
		watch: {
			musicId() {
				console.log(this.musicId)
			},
			currentTime() {
				console.log(this.currentTime)
			},
			mylyric(){
				if(this.mylyric.length > 3){
					this.mylyric = this.mylyric.slice(0,3)
				}
			}
		},
		computed: {
			...mapGetters(
				['getMusic', 'getCurrentTime']
			),
		},
		methods: {
			currentLyric(time) {
				if(this.time != time) {
					this.time = time
					if(this.lyric != null) {
						var num = time.split(':').reduce((a, b) => {
							return a * 60 + parseInt(b)
						})

						if(this.lyric[num] != '\n' && this.lyric[num]) {
							for(var i = 0; i <= this.mylyric.length; i++) {
								this.mylyric.unshift(this.lyric[num])
								if(this.mylyric[i] == this.lyric[num]) {
									break;
								}
							}
						}
					}
				}
			}
	}

	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	#lyric {
		display: flex;
		flex-direction: column;
		justify-content: space-around;
		height: 100%;
		flex-grow: 1;
	}
	
	.lyric-item {
		font-size: 14px;
		color: #4d4d4d;
	}
	
	.lyric-item:first-child {
		font-size: 18px;
		color: #111111;
	}
	
</style>


