<template>
	<div class="recommand">

		<div class="content">
			<swiper :options="swiperOptionIn" class="swiper-position">
				<swiper-slide v-for="(v,k) in slider" :key="k">
					<a :href="v.jumpurl">
						<img class="recommand-swiper-img" :src="v.pic">
					</a>
				</swiper-slide>
				<div class="swiper-pagination-white swiper-pagination-position" slot="pagination"></div>
			</swiper>
			
			
			<div class="hotlist">
        <div class="list-title">
          <p class="title-text">热门歌单</p>
        </div>
        <div class="list-content">
          <div class="list-item" v-for="item in songList">
            <div class="list-img">
              <img :src="item.imgurl">
              <div class="listen-count">
                <img src="../assets/icon-erji.svg" alt="">
                <span>{{item.listennum | listenCount}}</span>
              </div>
            </div>
            <div class="list-info">
              <p class="list-name">{{item.dissname}}</p>
              <p class="list-author">{{item.author}}</p>
            </div>
          </div>
        </div>
      </div>
		</div>
		
		<div class="mvitem" v-for="v in mvList">
    <div class="img-wrapper">
      <img class="mv-img" :src="v.picurl" alt="name">

    </div>
    <div class="mv-info">
      <p class="name">{{v.mvtitle}}</p>
      <p class="author">{{v.singer_name}}</p>
      <p class="date">发行时间:{{v.pub_date}}</p>
      <div class="mv-playcount">
        <span>播放量:{{v.listennum | listenCount}}</span>
      </div>
    </div>
  </div>
		
	</div>
</template>

<script>

	import {
		swiper,
		swiperSlide
	} from 'vue-awesome-swiper'

	export default {
		components: {
			swiper,
			swiperSlide,
		},
		data(){
			return{
				slider:[],
				songList:[],
				mvList:[],
				swiperOptionIn: {
          pagination: '.swiper-pagination-white',
          paginationClickable: true,
          autoplay: 3000,
        }
			}
		},
		mounted(){
			this.$store.dispatch('getRecommands').then((res) => {
				this.slider = res.data.data.focus
				this.songList = res.data.data.hotdiss.list
				this.mvList = res.data.data.shoubomv.all
			})
		},
		filters:{
			listenCount(input){
				return (input / 10000).toFixed(1) + '万'
			}
		}
	}
</script>

<style scoped>
	/*.recommand {
		margin-top: 50px;
	}*/
	
	.recommand .loading {
		color: #999;
		width: 100%;
		height: 150px;
		line-height: 150px;
		text-align: center;
	}
	
	.recommand .content .swiper-position {
		position: relative;
		cursor: pointer;
	}
	
	.recommand .content .swiper-position .recommand-swiper-img {
		width: 100%;
		display: block;
	}
	
	.recommand .content .swiper-pagination-position {
		position: absolute;
		z-index: 1;
		display: flex;
		flex-direction: row;
		justify-content: center;
	}
	
	.recommand .content .hotlist {
		display: flex;
		flex-direction: column;
		background: #fff;
		margin-top: 10px;
		box-shadow: 0 0 10px #DDD;
	}
	
	.recommand .content .hotlist .list-title {
		height: 50px;
		text-align: center;
		line-height: 50px;
	}
	
	.recommand .content .hotlist .list-content {
		display: flex;
		flex-direction: row;
		flex-wrap: wrap;
	}
	
	.recommand .content .hotlist .list-content .list-item {
		width: 50%;
		cursor: pointer;
	}
	
	.recommand .content .hotlist .list-content .list-item .list-img {
		position: relative;
	}
	
	.recommand .content .hotlist .list-content .list-item .list-img img {
		width: 100%;
	}
	
	.recommand .content .hotlist .list-content .list-item .list-img .listen-count {
		position: absolute;
		bottom: 5px;
		left: 5px;
		color: #fff;
		font-size: 12px;
		display: flex;
		flex-direction: row;
		align-items: center;
	}
	
	.recommand .content .hotlist .list-content .list-item .list-img .listen-count img {
		width: 15px;
		height: 15px;
		display: inline-block;
		margin-right: 3px;
	}
	.recommand .content .hotlist .list-content .list-item .list-img .listen-count span{
		padding: 5px;
	}
	.recommand .content .hotlist .list-content .list-item .list-info {
		padding: 0 5px;
		font-weight: 300;
		font-size: 14px;
		margin-bottom: 10px;
	}
	
	.recommand .content .hotlist .list-content .list-item .list-info .list-name {
		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
	}
	
	.recommand .content .hotlist .list-content .list-item .list-info .list-author {
		color: #777777
	}
	.mvitem {
    background: #fff;
    box-shadow: 0 0 10px #DDD;
    margin: 5px 10px;
    display: flex;
    flex-direction: row;
    padding: 5px;
    cursor: pointer;
    overflow: hidden;
    
    

  }
  .mv-info {
      padding: 5px;
      font-weight: 300;
      font-size: 14px;
      position: relative;
      
      
    }
  .author, .date {
        color: #777;
        line-height: 25px;
      }
  .mv-playcount {
        position: absolute;
        bottom: 5px;
        left: 5px;
        font-size: 14px;
        display: flex;
        flex-direction: row;
        align-items: center;
        color: #777;
      }
    .mv-img {
    	width: 55vw;
        height: 33vw;
        max-height: 150px;
        display: block;
        border-radius: 5px;
      }
</style>


