<template>
	<div id="play" class="music-play-page">
		<div class="play-top">
			<div class="playImg">
				<img :src="getMusic.musicImg">
			</div>
			<div class="time-bar">
				<div class="progress-box">
					<div class="progress" :style="{width:getProgress}">
						<span class="huakuai"></span>
					</div>
				</div>
				<div class="time-box">
					<span class="time">{{getCurrentTime}}</span>
					<span class="time-max">{{getDuration}}</span>
				</div>
			</div>
		</div>
		<div class="play-bottom">
			<div class="music-lrc">
				<div class="lrc-top">
					<h3 class="song-title">{{getMusic.musicTitle}}</h3>
					<span class="song-singer">{{getMusic.musicSonger}}</span>
				</div>
				<div class="lrc-bottom">
					<lyric></lyric>
				</div>
			</div>
			<div class="control">
	          <div><img :src="likeUrl"  @click="playLike"></div>
	          
	          <div><img :src="shangyiquUrl" @click="playFront"></div>
	                  
	          <div @click='play(getPauseBtn)'><img @click="pause" :src="getPauseBtn?pauseImg:playImg"></div>
	                   
	          <div><img :src="xiayiquUrl" @click="playNext"></div>
	           
	           
	           <router-link to="/PlayingList" tag="div">
	          	<img :src="listUrl">
	          </router-link>     
	          
			</div>
		</div>
		
		<router-link to="/" tag="div" id="backhome"><img src="../assets/icon-back-white.svg"/></router-link>
	</div>
	
	
</template>

<script>
	//歌词组件
	import Lyric from './Lyric'
	//状态管理
	import {mapGetters,mapActions} from 'vuex'
	
	export default {
		name: 'paly',
		data() {
			return {
				imgurl: 'https://y.gtimg.cn/music/photo/radio/track_radio_199_13_1.jpg',
				playImg: require('../assets/icon-play.png'),
				pauseImg: require('../assets/icon-pause.png'),
				likeUrl:require('../assets/icon-like.png'),
				shangyiquUrl:require('../assets/icon-shangyiqu.png'),
				listUrl:require('../assets/icon-list.png'),
				xiayiquUrl:require('../assets/icon-xiayiqu.png')
			}
		},
		methods: {
			...mapActions(
				['pause']
			),
			play(bl) {
				if(bl){
					document.getElementById('music').play()
				}else{
					document.getElementById('music').pause()
				}
			},
			playLike(){
				console.log('playLike')
			},
			playFront(){
				let 
					listlength = this.$store.state.playinglist.length,
					num = this.$store.state.music.musicNum
				if(listlength > 0 && num > -1){
					num == 0 ? num = (listlength - 1) : num--;
					this.$store.commit('updateMusicByList',num)
    				this.$store.commit('musicPlay')
				}
			},
			playNext(){
				let 
					listlength = this.$store.state.playinglist.length,
					num = this.$store.state.music.musicNum
				if(listlength > 0 && num > -1){
					num == (listlength - 1) ? num = 0 : num++;
					this.$store.commit('updateMusicByList',num)
    				this.$store.commit('musicPlay')
				}
				
			},
		},
		computed:{
			...mapGetters(
				['getPauseBtn','getDuration','getCurrentTime','getProgress','getMusic']
			),
		},
		components:{
			Lyric
		}
	}
</script>

<style>
	.music-play-page {
		width: 100%;
		max-width: 68vh;
		height: 100%;
		position: fixed;
		top: 0;
		z-index: 5;
		display: flex;
		flex-direction: column;
		background: #ccdddd;
	}
	
	.playImg {
		font-size: 0;
	}
	
	.playImg img {
		width: 100%;
	}
	
	.progress-box {
		width: 100%;
		height: 5px;
		background: #ccc;
	}
	
	.progress {
		height: 100%;
		width: 0;
		background: #FF2D55;
	}
	
	.huakuai {
		float: right;
		width: 2px;
		height: 10px;
		background: #FF2D55;
	}
	
	.time-box {
		box-sizing: border-box;
		width: 100%;
		padding: 0 8px;
		display: flex;
		justify-content: space-between;
	}
	.play-bottom{
		flex: 1;
		display: flex;
		flex-direction: column;
	}
	.music-lrc{
		flex: 1;
		text-align: center;
		display: flex;
		flex-direction: column;
	}
	.song-title{
		color: #111;
	}
	.song-singer{
		display: block;
		color: #A9A6A6;
		font-size: 12px;
		padding: 5px 0;
	}
	.music-lrc .lrc-bottom{
		flex: 1;
	}
	.control{
		width: 100%;
		height: 70px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	.control div{
		width: 15%;
		text-align: center;
	}
	.control div img{
		width: 60%;
	}
	
	
	
	#backhome{
		position: absolute;
		width: 30px;
		height: 30px;
		padding: 5px;
		text-align: center;
		line-height: 30px;
		color: #fff;
		background: rgba(0,0,0,.3);
		top: 10px;
		left: 10px;
		border-radius: 50%;
	}
	#backhome:active{
		background: rgba(0,0,0,.9);
		
	}
	#backhome img{
		width: 100%;
		height: 100%;
	}
</style>


