<template>
	<div id="app">
		<transition name="page-slide">
			<router-view></router-view>
	    </transition>
	    
		<transition name="bar-slide">
			<div id="play-bar" @click='play(getPauseBtn)'>
				<audio id="music" :src="getMusic.musicSrc" autoplay
					@timeupdate="updateTime"
					></audio>
				<router-link to="/play" tag="div" class="routerBtn">
				<div class="play-bar-image-container">
					<img class="play-bar-image" :src="getMusic.musicImg">
				</div>
				<p class="play-bar-text">{{getMusic.musicTitle}}-{{getMusic.musicSonger}}</p>
				</router-link>
				<img class="play-bar-button" @click="pause" :src="getPauseBtn?pauseImg:playImg">
			</div>
		</transition>
	</div>
</template>

<script>
	//轮播图插件
	import Vue from 'vue'

	//状态管理
	import {mapGetters,mapActions} from 'vuex'
	

	export default {
		name: 'app',
		data() {
			return {
				audioBtn: require('./assets/icon-play.png'),
				playImg: require('./assets/icon-play.png'),
				pauseImg: require('./assets/icon-pause.png'),
				pauseBtn: true,
			}
		},
		components: {
		},
		methods: {
			...mapActions(
				['pause']
			),
			play(bl) {
				if(bl){
					document.getElementById('music').play()
				}else{
					document.getElementById('music').pause()
				}
			},
			updateTime(){
				this.$store.commit('updateCurrentTime',document.getElementById('music').currentTime.toFixed(2))
				this.$store.commit('updateDuration',document.getElementById('music').duration.toFixed(2))
			}
		},
		computed:{
			...mapGetters(
				['searchShow','getPauseBtn','getMusic']
			),
		},
		mounted() {
		}
	}
</script>
	
<style>
	* {
		margin: 0;
		padding: 0;
	}
	
	html {
		overflow-x: hidden;
		background: #EEEEEE;
	}
	
	body {
		display: flex;
		overflow-x: hidden;
	}
	
	#app {
		font-family: -apple-system, BlinkMacSystemFont, "PingFang SC", "Helvetica Neue", STHeiti, "Microsoft Yahei", Tahoma, Simsun, sans-serif;
		width: 100%;
		height: 100%;
	}
	
	#app a {
		color: #42b983;
		text-decoration: none;
	}
	
	#play-bar {
		position: fixed;
		bottom: 0;
		width: 100%;
		height: 50px;
		background: -webkit-linear-gradient(top, #f9f9f9, #f3f3f3);
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		z-index: 2;
	}
	
	.play-bar-image-container {
		width: 37.5px;
		height: 37.5px;
		padding-left: 15px;
		cursor: pointer;
	}
	
	.play-bar-image {
		width: 37.5px;
		height: 37.5px;
		border-radius: 5px;
		display: inline-block;
	}
	
	.play-bar-text {
		flex-grow: 1;
		padding-left: 10px;
		cursor: pointer;
	}
	
	.play-bar-button {
		width: 20px;
		height: 20px;
		padding-right: 15px;
		cursor: pointer;
	}
	
	.page-slide-enter-active {
		transition: all .3s ease;
	}
	
	.page-slide-leave-active {
		transition: all .3s ease-out;
	}
	
	.page-slide-enter,
	.page-slide-leave-active {
		transform: translateX(100%);
	}
	
	.fade-enter-active {
		transition: all .3s ease;
	}
	
	.fade-leave-active {
		transition: all .3s ease-out;
	}
	
	.fade-enter,
	.fade-leave-active {
		opacity: 0;
	}
	
	.play-slide-enter-active {
		transition: all .3s ease;
	}
	
	.play-slide-leave-active {
		transition: all .3s ease-out;
	}
	
	.play-slide-enter,
	.play-slide-leave-active {
		transform: translateY(100vh);
	}
	
	.bar-slide-enter-active {
		transition: all .3s ease;
	}
	
	.bar-slide-leave-active {
		transition: all .3s ease-out;
	}
	
	.bar-slide-enter,
	.bar-slide-leave-active {
		margin-bottom: -50px;
	}
	
	@media screen and (min-width: 68vh) {
		body {
			width: 68vh;
			margin: 0 auto;
		}
		#play-bar {
			width: 68vh;
		}
	}
	/*border-1px 部分*/
	
	.border-1px {
		position: relative;
	}
	
	.border-1px-after:after {
		border-top: 1px solid #d0d0d0;
		content: ' ';
		display: block;
		width: 100%;
		position: absolute;
		left: 0;
	}
	
	.border-1px-before:before {
		border-top: 1px solid #d0d0d0;
		content: ' ';
		display: block;
		width: 100%;
		position: absolute;
		left: 0;
	}
	
	.border-1px:before {
		top: 0;
	}
	
	.border-1px:after {
		bottom: 0;
	}
	
	@media (-webkit-min-device-pixel-ratio: 1.5),
	(min-device-pixel-ratio: 1.5) {
		.border-1px:after,
		.border-1px:before {
			-webkit-transform: scaleY(.7);
			-webkit-transform-origin: 0 0;
			transform: scaleY(.7);
		}
		.border-1px:after {
			-webkit-transform-origin: left bottom;
		}
	}
	
	@media (-webkit-min-device-pixel-ratio: 2),
	(min-device-pixel-ratio: 2) {
		.border-1px:after,
		.border-1px:before {
			-webkit-transform: scaleY(.5);
			transform: scaleY(.5);
		}
	}
	
	@-webkit-keyframes imgFadeIn {
		from {
			opacity: 0;
		}
		to {
			opacity: 1;
		}
	}
	
	@keyframes imgFadeIn {
		from {
			opacity: 0;
		}
		to {
			opacity: 1;
		}
	}
	
	img[lazy=loaded] {
		-webkit-animation-duration: 1s;
		animation-duration: 1s;
		-webkit-animation-fill-mode: both;
		animation-fill-mode: both;
		-webkit-animation-name: imgFadeIn;
		animation-name: imgFadeIn;
	}
	
	img[lazy=error] {
		border-radius: 2px;
		-webkit-animation-duration: 1s;
		animation-duration: 1s;
		-webkit-animation-fill-mode: both;
		animation-fill-mode: both;
		-webkit-animation-name: imgFadeIn;
		animation-name: imgFadeIn;
	}
	
	.swiper-box {
		width: 100%;
		height: 100%;
		margin: 0 auto;
	}
	
	.swiper-item {
		height: 100%;
		padding: 55px 0;
	}
	
	.swiper-pagination-bullet-custom {
		width: 100% !important;
		height: 100% !important;
		margin: 0 !important;
		text-align: center;
		line-height: 50px;
		color: #999999;
		border-radius: 0 !important;
		background: #fff !important;
		opacity: 1 !important;
	}
	
	.swiper-pagination-bullet-custom.swiper-pagination-bullet-active {
		color: #000;
	}
	
	.swiper-pagination {
		top: 0;
		height: 50px;
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		width: 100%;
	}
	.routerBtn{
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
	}
</style>


