import * as types from './types'
export default {
	[types.SEARCH_SHOW](state) {
		state.searchShow = true;
	},
	[types.SEARCH_HIDE](state) {
		state.searchShow = false;
	},
	[types.PAUSE](state) {
		state.pauseBtn = !state.pauseBtn
	},

	//音乐播放
	updateCurrentTime(state, currentTime) {
		state.currentTime = currentTime
	},
	updateDuration(state, duration) {
		state.duration = duration
	},
	updateMusic(state, music) {
		state.music = music
	},

	//音乐快进
	playNext(state) {
		state.currentTime += 5
	},

	//获取音乐列表
	updatePlayList(state, playinglist) {
		state.playinglist = playinglist
	},

	//改变音乐循环模式
	changePlayMode(state) {
		state.PlayMode == 2 ? state.PlayMode = 0 : state.PlayMode++;
	},

	updateMusicByList(state, num) {
		var obj = {
			musicSrc: 'http://ws.stream.qqmusic.qq.com/' + state.playinglist[num].id + '.m4a?fromtag=46',
			musicTitle: state.playinglist[num].name,
			musicSonger: state.playinglist[num].singer,
			musicId: state.playinglist[num].id,
			musicImg: state.music.musicImg,
			musicNum: num,
		}
		state.music = obj
	},

	//播放状态变为true
	musicPlay(state) {
		state.pauseBtn = true;
	}
}


